import React from 'react';
import { graphql } from 'gatsby';
import { Heading, Box, Text, useColorMode } from '@chakra-ui/core';
import { MDXProvider } from '@mdx-js/react';
import { MDXRenderer } from 'gatsby-plugin-mdx';

import { Layout, SEO, Tags, PathFinder } from '../components';
import { MDXProviderComponents } from './MDXProviderComponents';
import { LocationContext } from '../context';

const BlogPostTemplate: React.FC<any> = props => {
  const { data, location } = props;
  const { colorMode } = useColorMode();

  const {
    mdx: {
      frontmatter: { title, date, path, tags },
      body: content,
    },
  } = data;

  return (
    <LocationContext.Provider value={location}>
      {/* <Layout> */}
        <SEO title={title} />
        <Box
          backgroundColor={`background.${colorMode}`}
          color={`text.${colorMode}`}
        >
          <Box m="0px auto" p="1.0875rem" maxWidth="700px" minHeight="100vh">
          <PathFinder/>
          <Heading mb={4}>{title}</Heading>
          <MDXProvider components={MDXProviderComponents()}>
            <MDXRenderer>{content}</MDXRenderer>
          </MDXProvider>
          <Tags tags={tags} />
          </Box>
        </Box>
      {/* </Layout> */}
    </LocationContext.Provider>
  );
};

export const blogPostsQuery = graphql`
  query($path: String!) {
    mdx(frontmatter: { path: { eq: $path } }) {
      body
      frontmatter {
        path
        title
        date(formatString: "MMMM DD, YYYY")
        tags
      }
    }
  }
`;

export default BlogPostTemplate;
